.signin-page{
    /* background-color: #A7727D; */
    background-image: url(../images/imagePd.png);
    background-repeat: no-repeat;
    background-position-x: center;
}
.page{
    display: flex;
    position: relative;
    height: 100%;
    width: 100%;
}
.logoSize{
    width: 100%;
    height: auto;
    margin-top: 5px;
}
.loginFormContainer{
    height: 570px;
    width: 490px;
    position: relative;
    margin: auto;
    top: 100px;
    margin-top: 50px;
    background-color: #F9F5E7;
    border-radius: 25px;
    box-shadow:0px 5px 15px 0 rgba(0,0,0,0.16);
}
.logoContainer{
    background : #F8EAD8 ;
    border-radius : 50%;
    width : 180px;
    height : 180px;
    align-items: flex-start;
    margin: auto;
    top: -90px;
    position: relative;
    box-shadow:0px 5px 15px 0 rgba(0,0,0,0.16);
}
.loginForm{
    color: #A7727D;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -45px;
}
.lBUser{
    margin-right: 5px;
}
.linkSignUp{
    font-size: 10px;
    text-align: end;
    padding-left: 150px;
}
.frameLinkSignUp{
    margin-left: auto;
}
.lBPwd{
    margin-right: 270px;
}
.inputTextUsername,
.inputTextPass{
    background-color: #F8EAD8;
    width: 350px;
    height: 40px;
    border-radius: 25px;
    border-style: none;
    box-shadow: inset 0px 0px 8px rgba(0, 0, 0, 0.2);
    padding-left: 20px;
}
.inputTextUsername::placeholder{
    color: #A7727D;
}
.inputTextPass::placeholder{
    color: #A7727D;
}
.inputPass{
    margin-bottom: 30px;
}
.btSignIn{
    width: 250px;
    height: 35px;
    border-radius: 25px;
    border-style: none;
    background-color: #A7727D;
    color: #fff;
    font-family: 'IBM Plex Sans Thai', sans-serif;
    font-size: 16px;
}
.btSignIn:hover{
    transition: 0.3s ease;
    opacity: 0.7;
}
.xt_1_hr {
    display: flex;
    align-items: center;
    margin: 20px 0;
}
.line {
    border: none;
    height: 1px;
    width: 150px;
    background-color: #616161;
    flex: 1;
    margin-left: 15px;
    margin-right: 15px;
}
.label1 {
    margin: 0 10px;
    color: #616161;
    font-size: 14px;
    font-weight: bold;
}
.iconGoogle{
    font-size: 20px;
    padding-right: 20px;
}
.bt_lwg{
    display: flex;
    width: 250px;
    height: 35px;
    font-size: 14px;
    align-items: center;
    justify-content: center;
    background-color: #EDDBC7;
    color: #A7727D;
    border-radius: 25px;
    border-style: none;
}
.bt_lwg:hover{
    transition: 0.3s ease;
    opacity: 0.7;
}
.plc_pvr{
    color: #A0A0A0;
    width: 80%;
    font-size: 12px;
    text-align: center;
}

@media screen and (min-width : 850px) and (max-width : 1080px) {
    .loginFormContainer{
        height: 490px;
        width: 390px;
        margin: auto;
        top: 70px;
        margin-top: 50px;
    }
    .logoContainer{
        width : 140px;
        height : 140px;
        top: -70px;
    }
    .lBUser{
        margin-right: 0px;
    }
    .labelUsername {
        font-size: 14px;
    }
    .linkSignUp{
        font-size: 10px;
        text-align: end;
        padding-left: 130px;
    }
    .inputTextUsername,
    .inputTextPass{
        background-color: #F8EAD8;
        width: 300px;
        height: 35px;
        padding-left: 20px;
    }
    .lBPwd{
        margin-right: 250px;
    }
    .labelPassword {
        font-size: 14px;
    }
    .inputPass{
        margin-bottom: 30px;
    }
    .btSignIn{
        width: 200px;
        height: 35px;
        border-radius: 25px;
        font-size: 14px;
    }
    .xt_1_hr {
        display: flex;
        align-items: center;
        margin: 20px 0;
    }
    .line {
        height: 1px;
        width: 100px;
        flex: 1;
    }
    .label1 {
        margin: 0 0px;
        font-size: 12px;
    }
    .iconGoogle{
        font-size: 18px;
        padding-right: 20px;
    }
    .bt_lwg{
        width: 200px;
        height: 35px;
        font-size: 12px;
    }
    .plc_pvr{
        font-size: 10px;
    }
}

