.chat-container {
    display: flex;
    margin: 20px;
}
.chat-myChat-box {
    display: flex;
    flex-direction: column;
    background-color: #F8EAD8;
    border-radius: 20px;
    width: 25%;
    height: 840px;
    margin-right: 20px;
    box-shadow:0px 5px 20px 1px rgba(0,0,0,0.16);
}
.chat-myChat-box-mobile {
    display: flex;
    flex-direction: column;
    background-color: #F8EAD8;
    border-radius: 20px;
    width: 100%;
    height: 840px;
    box-shadow:0px 5px 20px 1px rgba(0,0,0,0.16);
}
.chat-myChat-box-mobile-none {
    display: none;
}
.chat-chat-box {
    display: flex;
    flex-direction: column;
    width: 75%;
    height: 840px;
    background-color: #F8EAD8;
    border-radius: 20px;
    box-shadow:0px 5px 20px 1px rgba(0,0,0,0.16);
}
.chat-chat-box-mobile {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 840px;
    background-color: #F8EAD8;
    border-radius: 20px;
    box-shadow:0px 5px 20px 1px rgba(0,0,0,0.16);
}
.chat-chat-box-mobile-none {
    display: none;
}
.chat-myChat-header {
    display: flex;
    align-items: center;
    margin: 30px auto 30px auto;
}
.chat-myChat-header label{
    font-size: 27px;
    color: #A7727D;
    font-weight: bold;
    margin-left: 15px;
}
.chat-myChat-profile {
    transition: 0.3s ease;
    display: flex;
    background-color: #F9F5E7;
    margin: 0 auto 20px auto;
    border-radius: 20px;
    height: 70px;
    /* width: 350px; */
    width: 80%;
    box-shadow:0px 5px 20px -5px rgba(0,0,0,0.16);
}
.chat-myChat-profile-selected {
    transition: 0.3s ease;
    display: flex;
    background-color: #A7727D;
    margin: 0 auto 20px auto;
    border-radius: 20px;
    height: 70px;
    /* width: 350px; */
    width: 80%;
    color: #FFFFFF;
    box-shadow:0px 5px 20px -5px rgba(0,0,0,0.16);
}
.chat-myChat-profile img{
    width: 50px;
    height: 50px;
    margin: auto 20px auto 20px;
    border-radius: 30px;
}
.chat-myChat-profile-selected img{
    width: 50px;
    height: 50px;
    margin: auto 20px auto 20px;
    border-radius: 30px;
}
.chat-myChat-profile-text-box {
    margin: auto 0 auto 0;
}
.chat-myChat-profile-latest-bold {
    font-weight: bold;
    color: #A7727D;
}
.chat-myChat-profile-latest-bold-selected {
    font-weight: bold;
    color: #FFFFFF;
}
.chat-myChat-scroll-box {
    overflow-y: auto;
    width: auto; 
    height: 710px;
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.chat-myChat-scroll-box::-webkit-scrollbar {
    display: none;
}
.chat-chat-header {
    display: flex;
    align-items: center;
}
.chat-chat-not-selected {
    margin: auto;
    font-size: 40px;
    color: #A7727D;
    /* font-weight: bold; */
}
.chat-chat-back {
    margin: 20px 0 0 30px;
}
.chat-chat-name-display {
    display: flex;
    margin: 20px auto 0 auto;
}
.chat-chat-name-display img {
    width: 80px;
    height: 80px;
    border-radius: 40px;
    margin: 0 20px 0 0;
}
.chat-chat-name-display label {
    font-size: 30px;
    margin: auto;
}
.chat-chat-display-box {
    margin: 25px;
    border-radius: 20px;
    background-color: #F9F5E7;
    height: 100%;
    box-shadow:0px 5px 20px 1px rgba(0,0,0,0.16);
}
.chat-chat-display {
    display: flex;
    height: 90%;
    overflow-y: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.chat-chat-display::-webkit-scrollbar {
    display: none;
}
.chat-chat-display-loading {
    margin: auto;
}
.chat-chat-input-box {
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.chat-chat-input-box img {
    height: 30px;
    margin: 0 25px 0 25px;
}
.chat-chat-input-box img:hover {
    cursor: pointer;
}
.chat-chat-input-box input {
    width: 80%;
    height: 0px;
    border: none;
    border-radius: 10px;
    box-shadow:0px 5px 10px 1px rgba(0,0,0,0.16);
    padding: 20px;
    font-size: 20px;
}
.chat-chat-messages {
    display: flex;
    flex-direction: column;
    margin: 30px;
    width: 100%;
    max-height: 590px;
    /* overflow-x: hidden;
    overflow-y: auto; */
}
.chat-chat-avatar {
    width: 35px;
    height: 35px;
    border-radius: 20px;
    margin-right: 5px;
}
.scrollable-feed::-webkit-scrollbar {
    width: 0; 
    height: 0; 
}
.scrollable-feed {
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.chat-chat-messages-one {
    display: flex;
}
#chat-chat-file-display-none {
    display: none;
}
#chat-chat-customFileLabel {
    display: inline-block;
    cursor: pointer;
}
.chat-chat-img-display {
    width: 250px;
    border-radius: 20px;
}

@media screen and (min-width: 1080px) and (max-width: 1919px){
    .chat-myChat-box {
        width: 40%;
        height: 840px;
        margin-right: 20px;
    }
    .chat-chat-box {
        width: 60%;
        height: 840px;
    }
}

@media screen and (min-width: 850px) and (max-width: 1080px){
    .chat-chat-name-display img {
        width: 60px;
        height: 60px;
        border-radius: 30px;
    }
    .chat-chat-name-display label {
        font-size: 20px;
    }
    .chat-chat-input-box img {
        height: 25px;
    }
    .chat-chat-input-box input {
        font-size: 17px;
    }
}

@media screen and (min-width: 0px) and (max-width: 850px){
    .chat-chat-name-display img {
        width: 60px;
        height: 60px;
        border-radius: 30px;
    }
    .chat-chat-name-display label {
        font-size: 20px;
    }
    .chat-chat-input-box img {
        height: 25px;
    }
    .chat-chat-input-box input {
        font-size: 17px;
    }
}